  align-content: center;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 100%;
}

.mdx-illustration-block li:after {
  content: " ";
  display: block;
  position: absolute;
  top: 50%;
  inset-inline-end: 100%;
  transform: translateY(-50%);
  width: 60px;
  height: 49px;
